<style lang="less">
  @import './styles/menu.less';
</style>

<template>
  <div :style="{background: bgColor}" class="ivu-shrinkable-menu">
    <slot name="top"></slot>
    <!-- 展开显示各个标题的内容和图标 -->
    <sidebar-menu 
        v-show="!shrink"
        :menu-theme="theme" 
        :menu-list="menuList" 
        :open-names="openNames"
        @on-change="handleChange"
    ></sidebar-menu>
    <!-- 关闭只显示图标不显示内容 -->
    <sidebar-menu-shrink 
        v-show="shrink"
        :menu-theme="theme" 
        :menu-list="menuList" 
        :icon-color="shrinkIconColor"
        @on-change="handleChange"
    ></sidebar-menu-shrink>
  </div>
</template>

<script>
import sidebarMenu from './components/sidebarMenu.vue';
import sidebarMenuShrink from './components/sidebarMenuShrink.vue';
import util from '@/common/js/util';
export default {
  name: 'shrinkableMenu',
  components: {
    sidebarMenu,
    sidebarMenuShrink
  },
  props: {
    shrink: {
      type: Boolean,
      default: false
    },
    menuList: {
      type: Array,
      required: true
    },
    theme: {
      type: String,
      default: 'dark',
      validator (val) {
        return util.oneOf(val, ['dark', 'light']);
      }
    },
    beforePush: {
      type: Function
    },
    openNames: {
      type: Array
    }
  },
  computed: {
    bgColor () {
      return this.theme === 'dark' ? '#495060' : '#fff';
    },
    shrinkIconColor () {
      return this.theme === 'dark' ? '#fff' : '#495060';
    }
  },
  methods: {
    handleChange (name) {
      let willpush = true;
      if (this.beforePush !== undefined) {
        if (!this.beforePush(name)) {
          willpush = false;
        }
      }
      if (willpush) {
        this.$router.push({
          name: name
        });
      }
      this.$emit('on-change', name);
    }
  }
};
</script>



