<template>
  <Breadcrumb>
    <BreadcrumbItem 
        v-for="item in currentPath" 
        :href="item.path" 
        :key="item.name"
    >{{ itemTitle(item) }}</BreadcrumbItem>
  </Breadcrumb>
</template>

<script>
export default {
    name: 'breadcrumbNav',
    props: {
        currentPath: Array
    },
    methods: {
      itemTitle (item) {
      	return item.title;
        // if (typeof item.title === 'object') {
        // 	console.log(this)
        // 	// $t是做什么的，是使用vue-i18n插件国际化方法
        //   return this.$t(item.title.i18n);
        // } else {
        //   return item.title;
        // }
      }
    }
};
</script>


