<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  @import "./main.less";
</style>

<template>
  <div class="main" :class="{'main-hide-text': shrink}">
    <div class="sidebar-menu-box" :style="{width: shrink?'60px':'200px', overflow: shrink ? 'visible' : 'auto'}">
      <shrinkable-menu 
          :shrink="shrink"
          :menu-list="menuList"
          menu-theme="white">
          <div slot="top" class="logo">
            <img v-show="!shrink"  src="../../common/images/logo.jpg" key="max-logo" />
            <img v-show="shrink" src="../../common/images/logo-min.jpg" key="min-logo" />
          </div>
      </shrinkable-menu>
    </div>
    <div class="main-header-box" :style="{paddingLeft: shrink?'60px':'200px'}">
      <div class="main-header">
        <div class="navicon-box">
          <Button :style="{transform: 'rotateZ(' + (this.shrink ? '-90' : '0') + 'deg)'}" type="text" @click="toggleClick">
            <Icon type="md-menu" size="32"></Icon>
          </Button>
        </div>
        <div class="header-middle-box">
          <div class="main-breadcrumb">
            <!-- 面包屑组件  -->
            <breadcrumb-nav :currentPath="currentPath"></breadcrumb-nav>
          </div>
        </div>
        <div class="header-avator-box">
          <!-- 主题颜色切换组件 -->
          <!-- <theme-switch></theme-switch> -->
          <div class="user-dropdown-menu-box">
            <Row type="flex" justify="start" align="middle" class="user-dropdown-inner">
              <Dropdown transfer trigger="click" @on-click="handleClickUserDropdown">
                <a href="javascript:void(0)">
                  <span class="main-user-name">{{ userName }}</span>
                  <Icon type="md-arrow-dropdown"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem name="userInfo" divided>用户中心</DropdownItem>
                  <DropdownItem name="loginout" divided>退出登录</DropdownItem>
                </DropdownMenu>
              </Dropdown>
              <Avatar icon="ios-person" style="background: #619fe7;margin-left:10px;flex:1;"></Avatar>
            </Row>
          </div>
        </div>
      </div>
    </div>
    <div class="single-page-box" :style="{left: shrink?'60px':'200px'}">
      <div class="single-page">
        <router-view :key="routerKey"></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import shrinkableMenu from '@/components/shrinkable-menu/shrinkable-menu.vue';
// 面包屑组件
import breadcrumbNav from '@/components/breadcrumb-nav/breadcrumb-nav.vue';
// 主题颜色切换组件
// import themeSwitch from '@components/theme-switch/theme-switch.vue';
import {appRouter} from '@/router/router';
import util from '@/common/js/util.js';


export default {
  name: 'v-main', // 不能使用main命名，js特定符号
  data () {
    return {
      shrink: false, // fasle菜单缩小,true放大,控制菜单的显示大小
      userName: '',
      currentPath: [
        {
          title: '首页',
          path: '/home',
          name: 'home_index'
        }
      ]
    }
  },
  mounted () {
      this.init()
  },
  computed: {
    routerKey() {
      // 为路由增加key值，解决创建和编辑的页面使用的是同一个component,默认情况下当这两个页面切换时不会触发vue的created或者mounted钩子
      return this.$route.name !== undefined ? this.$route.name + +new Date() : this.$route + +new Date();
    },
    menuList () {
      return appRouter; // 路由配置作为菜单项目
    }
  },
  watch: {
    $route (to) {
      // console.log(this.$route)
      // console.log(to.name)
      // 设置面包屑路由路径
      this.currentPath = util.setCurrentPath(this, to.name);
    }
  },
  methods: {
    handleClickUserDropdown (name) {
      // console.log(name)
      if(name == 'loginout'){
        sessionStorage.removeItem('token')
        sessionStorage.removeItem('username')
        this.$router.push({
          name: 'Login'
        })
      }
    },
    toggleClick () {
      this.shrink = !this.shrink
    },
    init () {
      this.userName = sessionStorage.getItem('username')
      // 设置面包屑路由路径
      this.currentPath = util.setCurrentPath(this, this.$route.name)
      // this.userName = Cookies.get('user')
    }
  },
  components: {
    shrinkableMenu,
    breadcrumbNav,
    // themeSwitch 
  }
}
</script>





